import React, { Component } from 'react';
import {Redirect, Route} from 'react-router-dom';

function PrivateRoute ({ component: Com, ...rest }){
	return (<div>
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
				? <Com {...rest}/>
				:(<Redirect
					to={{
						pathname: "/login",
						state: location}}
				/>)
			}
		/>
		</div>
	);
}

export default PrivateRoute;