import React, { Component } from 'react'
import {NavLink, Switch, withRouter, Router, Redirect} from 'react-router-dom'
import RouteWithSubRoutes from './RouteWithSubRoutes';

function Doc({routes}){
    return (<div>
        <div style={{float:'left',width:'200px'}}>
            {
                routes.map((route, i)=>(
                    <RouteWithSubRoutes key={i} {...route} />
            ))}
            <Redirect from='./doc' to='./doc/core'/>
        </div>
        <div style={{float:'left',marginLeft:'700px'}}>
            <ol>
                <li><NavLink exact to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
        </div>
    </div>
)
}    

export default withRouter(Doc);
